<template>
    <div class="loading" v-show="isShow">
        <div class="loading-box">
            <van-loading type="spinner" color="#1989fa" />
        </div>
    </div>
</template>

<script>
    export default {
        name: "z-loading",
        computed:{
            isShow () {
                return this.$store.state.isShow;
            }
        },
    }
</script>

<style scoped>
    .loading{
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.3);z-index: 99999;
    }
    .loading-box{
        width: 2rem;
        height: 2rem;
        position: absolute;
        left: 50%;
        top: 50%;

    }
</style>