import Vue from 'vue';
import Vuex from 'vuex';
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex);
import {getcartdata} from "../api/cart";
import {SUCCESS} from "../config/base";


let store=new Vuex.Store({
    state:{
        token:'',
        isShow:false,
        uid:'',
        cart:null,
        allsta:0,
    },
    getters:{
        goodsnum:function(state){
            return function(gid){
                let n=0;
                if (!state.cart){
                    n=0;
                }else{
                    let goods=state.cart.goods.filter(ele=>ele.gid==gid)[0];
                    if (goods){
                        n=goods.num;
                    }
                }
                return n;
            }
        },
        //切换所有全选按钮状态
        changestatusall:(state)=>{
            let flag=false;
            if (state.cart){
                flag=state.cart.goods.every(ele=>ele.status==1);
            }
            return flag;
          // return state.cart.goods.every(ele=>ele.status==1);
        },
        changestatussome:(state)=>{
            let flag=false;
            if (state.cart){
                flag=state.cart.goods.some(ele=>ele.status==1);
            }
            return flag;
        }
    },
    mutations:{
        changeToken(state,token){
            state.token=token;
        },
        changeuid(state,token){
            state.uid=token;
        },
        toggleIsShow(state,show){
            state.isShow=show;
        },
        setCartData(state,cart){
            state.cart=cart;
        },

        incgoods(state,item){
            let {gid,gname,thumb,price}=item;
            console.log(state.cart);
            if (!state.cart){
                state.cart={
                    goods:[
                        {gid:gid,num:1,status:1,gname,thumb,sale:price}
                    ],
                    price:'',
                    total:'',
                    id:''
                };
                return;
            }
            let goods=state.cart.goods.filter(ele=>ele.gid===gid)[0];
            if (goods){
                goods.num++;
            }else{
             state.cart.goods.push({gid:gid,num:1,status:1,gname,thumb,sale:price});
            }
        },
        decgoods(state,gid){
            let goods=state.cart.goods.filter(ele=>ele.gid===gid)[0];
            if (goods.num==0){
                return;
            }
            if (goods.num>1){
                goods.num--;
            }else{
                goods.num--;
                let index=0;
                for (let i=0;i<state.cart.goods.length;i++){
                   if (state.cart.goods[i].gid===gid){
                       index=i;
                       console.log(index);
                   }
                }
                state.cart.goods.splice(index,1);
            }
        },
        calcTotal(state){
            let goods=state.cart.goods.filter(ele=>ele.status);
            let total=0;
            if (goods.length){
                total=goods.map(ele => ele.num).reduce((prev, current) => prev + current);
            }
            state.cart.total=total;
        },
        calcPrice(state){
            let goods=state.cart.goods.filter(ele=>ele.status);
            let price=0;
            if (goods.length){
               price=goods.reduce((prev, current) => {
                   return prev += current.num * current.sale;
                }, 0);
            }
            state.cart.price=price;
        },
        changestatus(state,gid){
            let goods=state.cart.goods.filter(ele=>ele.gid===gid)[0];
            return goods.status=goods.status?0:1;
        },
        changestaall(state,sta){
            return state.allsta=sta?0:1;
        },
        ordersFinish(state){
            state.cart.goods=state.cart.goods.filter(ele=>!ele.status);
            if (state.cart.goods.length){
                state.cart.total=0;
                state.cart.price=0;
            } else{
                state.cart=null;
            }
        }

    },
    plugins: [createPersistedState()],
    actions:{
        setCartData({commit}){
            getcartdata('/api/cart/2').then(res=>{
                if (res.data.code===SUCCESS&&res.data.data){
                        commit("setCartData",res.data.data);
                    }
                }
            )
        }
    }
});


export default store;
