import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'index',
    meta:{title:'生鲜系统'},
    component: () => import('../views/content/Index.vue')
  },
  {
    path: '/list',
    name: 'list',
    meta:{title:'列表'},
    component: () => import('../views/content/List.vue')
  },
  {
    path: '/classify',
    name: 'classify',
    meta:{title:'分类列表'},
    component: () => import('../views/content/Classify.vue')
  },
  {
    path: '/mine',
    name: 'mine',
    meta:{title:'我的',auth:true},
    component: () => import('../views/content/Mine.vue')
  },
  {
    path: '/mycart',
    name: 'mycart',
    meta:{title:'我的购物车',auth:true},
    component: () => import('../views/content/Mycart.vue')
  },
  {
    path: '/orders',
    name: 'orders',
    meta:{title:'订单详情',auth:true},
    component: () => import('../views/content/Orders.vue')
  },
  {
    path: '/pay',
    name: 'pay',
    meta:{title:'支付',auth:true},
    component: () => import('../views/content/Pay.vue')
  },
  {
    path: '/details',
    name: 'details',
    meta:{title:'商品详情'},
    component: () => import('../views/content/Details.vue')
  },
  {
    path: '/login',
    name: 'login',
    meta:{title:'登录'},
    component: () => import('../views/login/Login.vue')
  },
  {
    path: '/register',
    name: 'register',
    meta:{title:'注册'},
    component: () => import('../views/login/Register.vue')
  },
  {
    path: '/setup',
    name: 'setup',
    meta:{title:'设置',auth:true},
    component: () => import('../views/content/Setup.vue')
  },
  {
    path: '/perdata',
    name: 'perdata',
    meta:{title:'个人信息',auth:true},
    component: () => import('../views/content/Perdata.vue')
  },
  {
    path: '/username',
    name: 'username',
    meta:{title:'修改用户名',auth:true},
    component: () => import('../views/perdata/Username.vue')
  },
  {
    path: '/account',
    name: 'account',
    meta:{title:'账号安全',auth:true},
    component: () => import('../views/content/Account.vue')
  },
  {
    path: '/demo',
    name: 'demo',
    meta:{title:'demo'},
    component: () => import('../views/demo/demo1.vue')
  },
];

const router = new VueRouter({
  routes
});
router.beforeEach((to, from, next) => {
  document.title = to.meta.tittle || '每日生鲜系统';
  if (to.meta.auth) {
    if (store.state.token.trim()) {
      next();
    } else {
      next({name:'login',query:{redirect:to.name}});
    }
  } else {
    next();
  }
});

export default router
